'use client'

import { useState, useRef } from 'react'
import UploadProgress from '../../components/UploadProgress'

interface UserProfile {
  name: string
  email: string
  bio: string
  avatar?: string
}

export default function ProfilePage() {
  const [profile, setProfile] = useState<UserProfile>({
    name: 'John Doe',
    email: 'john.doe@example.com',
    bio: 'Content creator and video enthusiast'
  })
  
  const [isEditing, setIsEditing] = useState(false)
  const [uploading, setUploading] = useState(false)
  const [uploadProgress, setUploadProgress] = useState(0)
  const [uploadMessage, setUploadMessage] = useState('')
  const fileInputRef = useRef<HTMLInputElement>(null)

  const handleProfileUpdate = async () => {
    // Simulate API call
    await new Promise(resolve => setTimeout(resolve, 1000))
    setIsEditing(false)
    setUploadMessage('Profile updated successfully!')
    setTimeout(() => setUploadMessage(''), 3000)
  }

  const handleVideoUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0]
    if (!file) return

    // Validate file type
    if (!file.type.startsWith('video/')) {
      setUploadMessage('Please select a valid video file')
      setTimeout(() => setUploadMessage(''), 3000)
      return
    }

    // Validate file size (50MB limit for demo)
    if (file.size > 50 * 1024 * 1024) {
      setUploadMessage('File size must be less than 50MB')
      setTimeout(() => setUploadMessage(''), 3000)
      return
    }

    setUploading(true)
    setUploadProgress(0)
    setUploadMessage('')

    try {
      // Create form data
      const formData = new FormData()
      formData.append('video', file)

      // Simulate upload progress
      const interval = setInterval(() => {
        setUploadProgress(prev => {
          if (prev >= 95) {
            clearInterval(interval)
            return 95
          }
          return prev + Math.random() * 15
        })
      }, 200)

      // Make actual API call
      const response = await fetch('/api/upload', {
        method: 'POST',
        body: formData,
      })

      const result = await response.json()
      
      // Complete progress
      setUploadProgress(100)
      
      if (result.success) {
        setUploadMessage('Video uploaded successfully!')
        setTimeout(() => setUploadMessage(''), 3000)
      } else {
        setUploadMessage(result.error || 'Upload failed. Please try again.')
        setTimeout(() => setUploadMessage(''), 3000)
      }
      
    } catch (error) {
      setUploadMessage('Upload failed. Please try again.')
      setTimeout(() => setUploadMessage(''), 3000)
    } finally {
      setUploading(false)
      setUploadProgress(0)
      if (fileInputRef.current) {
        fileInputRef.current.value = ''
      }
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-b from-white to-gray-50">
      {/* Header */}
      <div className="bg-white border-b border-gray-100">
        <div className="px-6 py-8">
          <div className="flex items-center justify-between mb-6">
            <h1 className="text-2xl font-bold text-gray-900">Profile</h1>
            <button
              onClick={() => setIsEditing(!isEditing)}
              className={`px-4 py-2 rounded-xl font-medium transition-all duration-200 ${
                isEditing 
                  ? 'bg-gray-100 text-gray-700 hover:bg-gray-200' 
                  : 'bg-black text-white hover:bg-gray-800'
              }`}
            >
              {isEditing ? 'Cancel' : 'Edit'}
            </button>
          </div>

          {/* Profile Avatar */}
          <div className="flex items-center space-x-4 mb-6">
            <div className="w-20 h-20 bg-gradient-to-br from-gray-800 to-black rounded-2xl flex items-center justify-center">
              <div className="w-10 h-10 bg-white rounded-xl"></div>
            </div>
            <div className="flex-1">
              <h2 className="text-xl font-semibold text-gray-900">{profile.name}</h2>
              <p className="text-gray-600">{profile.email}</p>
            </div>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="px-6 py-6 space-y-6">
        {/* Profile Information */}
        <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">Personal Information</h3>
          
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Full Name</label>
              {isEditing ? (
                <input
                  type="text"
                  value={profile.name}
                  onChange={(e) => setProfile({...profile, name: e.target.value})}
                  className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:ring-2 focus:ring-black focus:border-transparent transition-all duration-200"
                  placeholder="Enter your full name"
                />
              ) : (
                <div className="px-4 py-3 bg-gray-50 rounded-xl text-gray-900">{profile.name}</div>
              )}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Email Address</label>
              {isEditing ? (
                <input
                  type="email"
                  value={profile.email}
                  onChange={(e) => setProfile({...profile, email: e.target.value})}
                  className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:ring-2 focus:ring-black focus:border-transparent transition-all duration-200"
                  placeholder="Enter your email"
                />
              ) : (
                <div className="px-4 py-3 bg-gray-50 rounded-xl text-gray-900">{profile.email}</div>
              )}
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">Bio</label>
              {isEditing ? (
                <textarea
                  value={profile.bio}
                  onChange={(e) => setProfile({...profile, bio: e.target.value})}
                  rows={3}
                  className="w-full px-4 py-3 border border-gray-200 rounded-xl focus:ring-2 focus:ring-black focus:border-transparent transition-all duration-200 resize-none"
                  placeholder="Tell us about yourself"
                />
              ) : (
                <div className="px-4 py-3 bg-gray-50 rounded-xl text-gray-900">{profile.bio}</div>
              )}
            </div>

            {isEditing && (
              <button
                onClick={handleProfileUpdate}
                className="w-full bg-black text-white py-3 px-6 rounded-xl font-medium hover:bg-gray-800 transition-all duration-200"
              >
                Save Changes
              </button>
            )}
          </div>
        </div>

        {/* Video Upload Section */}
        <div className="bg-white rounded-2xl shadow-sm border border-gray-100 p-6">
          <h3 className="text-lg font-semibold text-gray-900 mb-4">Add New Video</h3>
          
          {uploading ? (
            <UploadProgress progress={uploadProgress} />
          ) : (
            <div className="space-y-4">
              <div className="border-2 border-dashed border-gray-200 rounded-xl p-8 text-center hover:border-gray-300 transition-colors">
                <div className="w-16 h-16 bg-gray-100 rounded-2xl flex items-center justify-center mx-auto mb-4">
                  <div className="w-8 h-8 bg-gray-400 rounded-lg"></div>
                </div>
                <h4 className="text-lg font-medium text-gray-900 mb-2">Upload your video</h4>
                <p className="text-gray-600 mb-4">Choose a video file to add to your collection</p>
                
                <input
                  ref={fileInputRef}
                  type="file"
                  accept="video/*"
                  onChange={handleVideoUpload}
                  className="hidden"
                  id="video-upload"
                />
                <label
                  htmlFor="video-upload"
                  className="inline-block bg-black text-white py-3 px-6 rounded-xl font-medium hover:bg-gray-800 transition-all duration-200 cursor-pointer"
                >
                  Choose Video File
                </label>
                
                <p className="text-xs text-gray-500 mt-3">
                  Supported formats: MP4, MOV, AVI â€¢ Max size: 50MB
                </p>
              </div>
            </div>
          )}
        </div>

        {/* Success/Error Messages */}
        {uploadMessage && (
          <div className={`p-4 rounded-xl ${
            uploadMessage.includes('success') 
              ? 'bg-green-50 text-green-800 border border-green-200' 
              : 'bg-red-50 text-red-800 border border-red-200'
          }`}>
            <p className="font-medium">{uploadMessage}</p>
          </div>
        )}
      </div>
    </div>
  )
}
