interface UploadProgressProps {
  progress: number
}

export default function UploadProgress({ progress }: UploadProgressProps) {
  return (
    <div className="space-y-4">
      <div className="flex items-center justify-center">
        <div className="w-20 h-20 bg-gradient-to-br from-black to-gray-700 rounded-2xl flex items-center justify-center">
          <div className="w-10 h-10 bg-white rounded-xl animate-pulse"></div>
        </div>
      </div>
      
      <div className="text-center">
        <h4 className="text-lg font-semibold text-gray-900 mb-2">Uploading your video...</h4>
        <p className="text-gray-600 mb-4">Please don't close this page</p>
        
        <div className="w-full bg-gray-200 rounded-full h-3 mb-2">
          <div 
            className="bg-gradient-to-r from-black to-gray-700 h-3 rounded-full transition-all duration-300 ease-out"
            style={{ width: `${Math.min(progress, 100)}%` }}
          ></div>
        </div>
        
        <div className="flex justify-between text-sm text-gray-600">
          <span>{Math.round(progress)}% complete</span>
          <span>{progress < 100 ? 'Uploading...' : 'Processing...'}</span>
        </div>
      </div>
      
      {progress >= 100 && (
        <div className="text-center">
          <div className="inline-flex items-center space-x-2 text-green-600 font-medium">
            <div className="w-5 h-5 bg-green-500 rounded-full flex items-center justify-center">
              <div className="w-2 h-2 bg-white rounded-full"></div>
            </div>
            <span>Upload complete!</span>
          </div>
        </div>
      )}
    </div>
  )
}
